import React from 'react';
import './BannerBackground.css';
import 'antd/dist/antd.css';

import { Typography, Button } from 'antd';

const { Title } = Typography;

class BannerBackground extends React.Component {
  render() {
    return (
      <div className="banner-bg">
        <Typography className="title-bg">
          <Title style={{ color: 'white' }} level={2}>
            Tm gia s gii Bmentor
          </Title>
          <Title level={4} style={{ color: 'white' }}>
            Hy  chng ti gip bn gii hn v i xa hn vi nn tng gia s
            cng ngh 4.0
          </Title>
          <Title level={3} style={{ color: 'white' }}>
            Nhanh chng - Ch ng - Min ph!
          </Title>
        </Typography>
        <div className="btn-action-bg">
          <Button className="btn-action" size="large">
            Tm gia s
          </Button>
          <Button className="btn-action" size="large">
            Tr thnh gia s
          </Button>
        </div>
      </div>
    );
  }
}
export default BannerBackground;
