import React from 'react';
import './SideBarHirer.css';
import { Link } from 'react-router-dom';
import 'antd/dist/antd.css';
import { Icon, Layout, Menu } from 'antd';

const { Sider } = Layout;
const { SubMenu } = Menu;

class SideBarHirer extends React.Component {
  render() {
    return (
      <div>
        <Sider width={250} style={{ background: '#fff' }}>
          <Menu
            mode="inline"
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            className="sider-bar"
          >
            <SubMenu
              className="item-sider"
              key="sub1"
              title={
                <span>
                  <Icon type="user" />
                  Thng tin c nhn
                </span>
              }
            >
              <Menu.Item key="1">
                <Link to="/hirer-manage/profile">Sa thng tin c nhn</Link>
              </Menu.Item>
              <Menu.Item key="2">
                <Link to="/hirer-manage/change-password">
                  Thay i mt khu
                </Link>
              </Menu.Item>
            </SubMenu>
            <SubMenu
              className="item-sider"
              key="sub2"
              title={
                <span>
                  <Icon type="laptop" />
                  Qun l yu cu
                </span>
              }
            >
              <Menu.Item key="5">Gia s ang ch</Menu.Item>
              <Menu.Item key="6">Gia s ang thu</Menu.Item>
              <Menu.Item key="7">Gia s  thu</Menu.Item>
            </SubMenu>
            <Menu.Item key="8" className="item-sider">
              <Icon type="heart" />
              Gia s  thch
            </Menu.Item>
            <Menu.Item key="9" className="item-sider">
              <Icon type="message" />
              Tin nhn
            </Menu.Item>
            <Menu.Item key="10" className="item-sider">
              <Icon type="bell" />
              Thng bo
            </Menu.Item>
            <Menu.Item key="11" className="item-sider">
              <Icon type="dollar" />
              Qun l Point
            </Menu.Item>
          </Menu>
        </Sider>
      </div>
    );
  }
}

export default SideBarHirer;
